<template>
  <el-container class="MainPage">
    <el-header>
      <h3>{{userInfo.title}}</h3>
    </el-header>
    <el-main style="margin:20px 50px;">
      <h3></h3>
       <el-tabs :tab-position="tabPosition" style="height: 200px;font-size:20px;">
        <el-tab-pane label="">
          <p></p>
          <p>/</p>
        </el-tab-pane>
        <el-tab-pane label="">
          <p></p>
          <p></p>
        </el-tab-pane>
        <el-tab-pane label="">
          <p></p>
          <p>/</p>
          <p></p>
          <p>//</p>
        </el-tab-pane>
        <el-tab-pane label="">
          <p></p>
          <p></p>
          <p>/</p>
        </el-tab-pane>
        <el-tab-pane label="">
          <p></p>
          <el-button @click="logout" style="margin-top:90px;margin-left:25px;"></el-button>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>

<style lang="scss" scoped>
</style>

<script>
import api from '../assets/api';

export default {
  name: 'MainPage',
  async mounted() {
    const userInfo = (await api.get('/userinfo')).data;
    this.userInfo = userInfo;
  },
  data() {
    return {
      userInfo: {},
      tabPosition: 'left',
    };
  },
  methods: {
    async logout() {
      const { data } = await api.get('/logout');
      if (data.code === 0) {
        this.$router.push('/login');
      } else {
        this.$message('');
      }
    },
  },
};
</script>
