<template>
  <el-container class='Forget'
    style="position:absolute; top:0;left:0;right:0;bottom:0; margin:auto;">
    <el-header >
      <h2 style="width:195px; margin:auto;"></h2>
    </el-header>
    <el-main>
      <el-form label-width="150px" style="width:800px; margin:50px auto;vertical-align:baseline;">
        <el-form-item label="" >
          <el-input v-model="email" style="width:455px;"></el-input>
          <el-button @click="forget" style="margin:20px;" type="primary"></el-button>
        </el-form-item>
      </el-form>
      <p v-show="fakeEmail" style="width:510px; margin:auto;">
        <a @click="changestatus" :href="changePath"></a>
      </p>
    </el-main>
    <el-footer style="width:200px; margin:50px auto;">
      &copy;
    </el-footer>
  </el-container>
</template>

<script>
import api from '../assets/api';

export default {
  name: 'Forget',
  data() {
    return {
      email: '',
      feedback: '',
      fakeEmail: false,
      changePath: '',
    };
  },
  methods: {
    async forget() {
      const { data } = await api.post('/forget', {
        email: this.email,
      });
      // console.log(data);
      if (data.code === -1) {
        this.feedback = '';
      } else {
        this.feedback = '20';
        this.fakeEmail = true;
        this.changePath = data.path;
      }
      this.$message(this.feedback);
    },
    changestatus() {
      this.fakeEmail = false;
    },
  },
};
</script>
