      style="backgroundColor:lightblue;">
      </el-page-header>
    </el-header>
    <el-main>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <h3></h3>
          <h4>{{orderInfo.id}}</h4>
          <h4>{{orderInfo.deskName}}</h4>
        </div>
        <div>
          <h3></h3>
          <div v-for="(foodInfo, index) in orderFoods" :key="index">
            {{foodInfo.id}}{{foodInfo.amount}}
          </div>
          <h3> {{orderInfo.totalPrice}}</h3>
        </div>
      </el-card>
    </el-main>
  </el-container>
</template>
<script>
import api from '../assets/api';

export default {
  name: 'OrderDetails',
  async mounted() {
    const orderInfo = (await api.get(`r/${this.$route.params.rid}/o/${this.$route.params.oid}`)).data;
    this.orderInfo = orderInfo;
    // console.log(this.orderInfo);
  },
  data() {
    return {
      orderInfo: {},
    };
  },
  computed: {
    orderFoods() {
      return this.orderInfo.details;
    },
  },
  methods: {
    goBack() {
      this.$router.push('/manage/1/orders');
    },
  },
};
</script>
