<template>
  <el-container class="Register"
  style="position:absolute; top:0;left:0;right:0;bottom:0; margin:auto;">
    <el-header >
      <h2 style="width:195px; margin:auto;"></h2>
    </el-header>
    <el-main>
      <el-form label-width="80px" style="width:400px;">
        <el-form-item label=""><el-input type="text" v-model="name"></el-input></el-form-item>
        <el-form-item label=""><el-input type="text" v-model="email"></el-input></el-form-item>
        <el-form-item label=""><el-input type="text" v-model="password"></el-input></el-form-item>
        <el-form-item label=""><el-input type="text" v-model="title"></el-input></el-form-item>
        <el-form-item><el-button @click='register' type="primary"></el-button></el-form-item>
      </el-form>
    </el-main>
    <el-footer style="width:200px; margin:auto;">
      &copy;
    </el-footer>
  </el-container>
</template>

<script>
import api from '../assets/api';

export default {
  name: 'Register',
  data() {
    return {
      name: '',
      password: '',
      email: '',
      title: '',
    };
  },
  methods: {
    async register() {
      if (this.name === '' || this.password === '' || this.email === '' || this.title === '') {
        this.$message('');
        return;
      }
      const { data } = await api.post('http://47.96.148.104/api/register', {
        name: this.name,
        password: this.password,
        email: this.email,
        title: this.title,
      });
      if (data.code === 0) {
        this.$message('');
        setTimeout(() => {
          this.$router.push('/login');
        }, 3000);
      }
      if (data.code === -1) {
        this.$message('');
      }
    },
  },
};
</script>
<style lang="scss" scoped>
  .Register{
    width:400px;
    margin:0 auto;
  }
</style>
