<template>
  <el-container class='ManageFood'>
    <el-main>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <h3></h3>
        </div>
        <div v-for="(food, index) in foodList" :key="index">
          <h3>{{food.name}}</h3>
          <div :style="cardStyle">
            <div style="overflow:hidden;">
              <img :src="'http://47.96.148.104/upload/' + food.img"  :alt="food.name" :style="imgStyle">
              <div style="margin-left: 500px;">
                <p>{{food.desc}}</p>
                <p>{{food.price}}</p>
                <p>{{food.category ? food.category : '[]'}}</p>
              </div>
            </div>
            <div v-show='btnIdx == index ? true : false'>
              <el-form>
                <el-form-item label="">
                  <el-input v-model='food.name'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model='food.price'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model='food.desc'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model='food.category'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <input type="file" :file='food.img' @change="imgChange(food,$event)">
                </el-form-item>
                <el-button @click.prevent="save(food)"></el-button>
              </el-form>
            </div>
            <div style="position:absolute; right:5px; bottom:5px;">
              <el-button @click="modify(index)"></el-button>
              <el-button v-if="food.status == 'on'" @click="takeDown(food)"></el-button>
              <el-button v-if="food.status == 'off'" @click="upShelf(food)"></el-button>
              <el-button @click="deleteFood(food, index)" type="primary"></el-button>
            </div>
          </div>
        </div>
      </el-card>

    </el-main>
  </el-container>
</template>


<style lang="scss" scoped>

</style>


<script>
import api from '../assets/api';

export default {
  name: 'ManageFood',
  async mounted() {
    const { data } = await api.get(`/restaurant/${this.$route.params.rid}/food`);
    this.foodList = data;
  },
  data() {
    return {
      foodList: [],
      imgStyle: {
        float: 'left',
        width: '100px',
        height: '100px',
        border: '1px solid',
      },
      cardStyle: {
        border: '2px solid',
        padding: '5px',
        position: 'relative',
        marginBottom: '50px',
      },
      btnIdx: -1,
    };
  },
  methods: {
    modify(idx) {
      this.btnIdx = idx;
    },
    imgChange(foodInfo, e) {
      // console.log(foodInfo);
      const img = e.target.files[0];
      // eslint-disable-next-line no-param-reassign
      foodInfo.img = img;
    },
    async save(foodInfo) {
      const fd = new FormData();
      const keys = Object.keys(foodInfo);
      const values = Object.values(foodInfo);
      for (let i = 0; i <= keys.length; i += 1) {
        if (Object.prototype.hasOwnProperty.call(values, i)) {
          fd.append(keys[i], values[i]);
        }
      }
      await api.put(`/restaurant/${this.$route.params.rid}/food/${foodInfo.id}`, fd);
      this.btnIdx = -1;
    },
    async takeDown(foodInfo) {
      // eslint-disable-next-line no-param-reassign
      foodInfo.status = 'off';
      await api.put(`/restaurant/${this.$route.params.rid}/food/${foodInfo.id}`, foodInfo);
    },
    async upShelf(foodInfo) {
      // eslint-disable-next-line no-param-reassign
      foodInfo.status = 'on';
      await api.put(`/restaurant/${this.$route.params.rid}/food/${foodInfo.id}`, foodInfo);
    },
    async deleteFood(foodInfo, idx) {
      // 
      this.foodList.splice(idx, 1);
      // 
      await api.delete(`/restaurant/${this.$route.params.rid}/food/${foodInfo.id}`);
    },
  },
};
</script>
